.blog {
    background: #F5F8FF;
    padding-top: 105px;
    padding-bottom: 203px;
    padding-left: 100px;
    padding-right: 100px;
    @media screen and (max-width: 400px) {
        padding: 35px;
    }
   
    &-subtitle {
        margin-bottom: 40px;
    }
    &__top {
        margin-bottom: 92px;
        @media screen and (max-width: 600px) {
            margin-bottom: 40px;
        }
    }
    &__title {
        @include text (24px, 500, 30px, #3D4F9F);
        font-family: 'Quicksand', sans-serif;
    }
    &__subtitle {
        @include text (10px, 500, 12px, #3D4F9F);
        font-family: 'Roboto', sans-serif;
        margin-bottom: 20px;
    }
    &__wrapper {
        padding: 63px 41px 64px 35px;
        // max-width: 958px;
        width: 100%;
        background-color: white;
        border: 1px solid #D8E3FE;
        margin: 0 auto;
        display: flex;
        column-gap: 50px;
        flex-wrap: wrap;
        @media screen and (max-width: 1220px) {
            justify-content: center;
        }
    }


&__row-icon {
    background-image: url(./../images/blog/left.svg);
    width: 27px;
    height: 19px;
    &--right {
        background-image: url(./../images/blog/right.svg);
        width: 27px;
        height: 19px;
    }
}
&__row {
    left: -150px;
    @media screen and (max-width: 1400px) {
        left: -100px;
    }
    @media screen and (max-width: 600px) {
        left: -60px;
    }
    @media screen and (max-width: 500px) {
        left: -50px;
    }
    &--right {
        right: -150px;
        @media screen and (max-width: 1400px) {
            right: -100px;
        }
        @media screen and (max-width: 600px) {
            right: -60px;
        }
        @media screen and (max-width: 500px) {
            right: -50px;
        }
    }

}

&__grid {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(2, 127px);
    gap: 11px;
    row-gap: 13px;
    @media screen and (max-width: 695px) {
        grid-template-rows: 1fr 1fr;
        grid-template-columns: 1fr;
        margin-bottom: 20px;
    }
    @media screen and (max-width: 600px) {
        display: none;
    }
}
&-img {
    object-fit: cover;
    border-radius: 5px;
}
&-img--big {
    max-width: 267px;
    width: 100%;
    grid-area: 1/1/3/3;
    height: 267px;
    @media screen and (max-width: 695px) {
        grid-area: 1/1/3/3;
    }
}
&-img--sm {
    max-width: 127px;
    width: 100%;
    grid-area: 1/3/2/4;
    height: 126px;
    @media screen and (max-width: 695px) {
        grid-area: 3/1/4/2;
    }
}
&-img--sm-bottom {
    max-width: 127px;
    width: 100%;
    height: 126px;
    grid-area: 2/3/3/4;
    @media screen and (max-width: 695px) {
        grid-area: 3/2/4/3;
    }
}
&__content {
    max-width: 408px;
    display: flex;
    flex-direction: column;
}

&-btn--blue {
    width: 113px;
    margin-right: 8px;
    color: #7496e0 !important;
    background-color: #d2e1ff;
    border: none;
}

&__heading {
    @include text (36px, 400, 45px, #3D4F9F);
    font-family: 'Quicksand', sans-serif;
    margin-bottom: 32px;
    @media screen and (max-width: 680px) {
        text-align: center;
    }
}
&__text {
    @include text (14px, 400, 27px, #8D96BD);
    display: block;
    max-width: 408px;
    margin-bottom: 40px;
    @media screen and (max-width: 1028px) {
        font-size: 17px;
    }
    @media screen and (max-width: 576px) {
        font-size: 14px;
    }
}
    &__carousel {
        bottom: -122px;
        @media screen and (max-width: 695px) {
            bottom: -50px;
        }    
}
    
    &__carousel li {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #D1D6E3;
        margin-right: 5px;
        margin-left: 5px;
    }

}